import React from "react";
import { Grid, Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import store from "src/store";
import clsx from "clsx";

const useStyles = makeStyles(theme => ({
  bg: {
    backgroundImage: "url('./images/header.png')",
    backgroundSize: "cover"
    // paddingTop: 56
  },
  logo: {
    minWidth: 80,
    width: "15vw"
  },
  box: {
    textAlign: "center",
    padding: "8vw 0 8vw  0 "
  },
  biglogo: {
    width: "80%"
  },
  graid: {
    margin: "auto",
    fontSize: "1vw"
  },
  language: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    color: "white"
  },
  button: {
    minWidth: 100,
    minHeight: 54,
    maxWidth: 150,
    maxHeight: 80,
    width: "15vw",
    height: "10vw",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"
    // marginRight: "3vw"
  },
  flag: {
    maxWidth: 30,
    minwidth: 10,
    width: "5vw",
    marginRight: 10
  },
  active: {
    backgroundImage: "url('./images/language.png')"
  }
}));
const Header: React.FC = () => {
  const theme = {
    spacing: 10
  };
  const classes = useStyles(theme);
  return (
    <div className={classes.bg}>
      <Container>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          className={classes.graid}
        >
          <img src="./images/logo1.png" alt="" className={classes.logo} />
          <div className={classes.language}>
            <div
              onClick={() => {
                store.i18n.changeLang("xi-GR");
              }}
              className={clsx(classes.button, {
                [classes.active]: store.i18n.locale == "xi-GR"
              })}
            >
              <img src="./images/Greece.png" alt="" className={classes.flag} />
              Ελλάδα
          </div>

            <div
              onClick={() => {
                store.i18n.changeLang("en-US");
              }}
              className={clsx(classes.button, {
                [classes.active]: store.i18n.locale == "en-US"
              })}
            >
              <img src="./images/English.png" alt="" className={classes.flag} />
              English
          </div>
            <div
              onClick={() => {
                store.i18n.changeLang("zh-CN");
              }}
              className={clsx(classes.button, {
                [classes.active]: store.i18n.locale == "zh-CN"
              })}
            >
              <img src="./images/china.png" alt="" className={classes.flag} />
              中文
          </div>
          </div>
        </Grid>
      </Container>
      <Container>
        <div className={classes.box}>
          <img src="./images/logo.png" alt="" className={classes.biglogo} />
        </div>
      </Container>
    </div>
  );
};
export default Header;
