export const number = /^[0-9]{1}\d*(.\d{1,8})?$|^0.\d{1,8}$/
export const address = /^0x+([0-9a-zA-Z]{40})?$/

export const validate = async (schema: any, values: any) => {
  let errors: any = {};
  for (let o in values) {
    try {
      await schema.validateAt(o, values);
    } catch (error) {
      errors[o] = error.message;
    }
  }
  return errors;
};


export const jsonTrim = (values:any)=>{
  for (const key in values) {
    values[key] = values[key].trim();
  }
  return values;
} 