import React from "react";

import IButton from "./common/IButton";
import { useLocalStore, useObserver } from "mobx-react";
import cobweb from "src/utils/cobweb";
import { error, isAddressNo } from "src/utils/tools";
import { toast } from "react-toastify";
import { action, observable } from "mobx";
import _store from "src/store";

const { i18n } = _store.i18n;

class PermWithdrawStore {
  @observable
  data = {
    idxInvest: "0",
    permAddress: "0x0000000000000000000000000000000000000000",
    status: "0",
    withdraw: "0"
  };

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobCore, account } = await cobweb();
      const data = await cobCore.methods.getPerm(account).call();
      // console.log(data);
      if (!!data) {
        this.data = {
          idxInvest: data["idxInvest"],
          permAddress: data["permAddress"],
          status: data["status"],
          withdraw: data["withdraw"],
        };
      }
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async withdraw() {
    const { cobWeb, account } = await cobweb();
    try {
      await cobWeb.methods.permWithdraw("0").send({ from: account });
      // 提现成功
      toast.success(i18n("1"));
    } catch (err) {
      error(err);
    }
  }
}

const store = new PermWithdrawStore();

const PermWithdraw: React.FC = () => {
  const iStore = useLocalStore(() => store);
  return useObserver(() => (
    <div>
      {!isAddressNo(iStore.data.permAddress) && (
        <>
          {/* <h3>节点用户</h3> */}
          {/* 节点用户提现 */}
      <IButton onTap={iStore.withdraw} color="#003DAA">{i18n("92")}</IButton>
        </>
      )}
    </div>
  ));
};

export default PermWithdraw;
