import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { green } from "@material-ui/core/colors";
// import Button from "@material-ui/core/Button";
import Button, { ButtonProps as MuiButtonProps } from '@material-ui/core/Button';
import { Omit } from '@material-ui/types';
import { useLocalStore, useObserver } from "mobx-react";

const useStyles = makeStyles({
  root: {
    // background:'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    display: "flex",
    alignItems: "center"
  },
  wrapper: {
    display:"flex",
    justifyContent:"center",
    // margin:"auto",
    // position: "relative"
  },
  buttonProgress: {
    color: green[500],
    position: "absolute",
    top: "50%",
    left: "50%",
    marginTop: -12,
    marginLeft: -12
  },
  button:{
    // background:"linear-gradient(45deg,#444442 30%, #262422 90%)",
    background: (props: Props) =>
    {
      return props.color ? props.color : "linear-gradient(45deg,#444442 30%, #262422 90%)";
    },
    borderRadius:20,
    padding:"5px 2vw",
    marginTop:"3vw"
  }
});

interface Props {
  onTap?: any;
  children?: any;
  color?:any;
}

const IButton: React.FC<Props> = (props: Props & Omit<MuiButtonProps, keyof Props>) => {
  const classes = useStyles(props);
  const { onTap,children, ...other } = props;
  const iStore = useLocalStore(() => ({
    loading: false,
    async handClick() {
      if (!iStore.loading) {
        iStore.loading = true;
        if (!!onTap) await onTap();
        await new Promise(resolve => setTimeout(resolve, 1000));
        iStore.loading = false;
      }
    }
  }));
  return useObserver(() => (
  
      <div className={classes.wrapper}>
        <Button
          variant="contained"
          color="primary"
          disabled={iStore.loading}
          onClick={iStore.handClick}
          className={classes.button}
        >
          {children}
        </Button>
        {iStore.loading && (
          <CircularProgress size={24} className={classes.buttonProgress} />
        )}
      </div>
  
  ));
};

export default IButton;
