import store from "src/store";
import ILine from "./common/ILine";

import copy from "copy-to-clipboard";
import ILevel from "./common/ILevel";
import _store from "src/store";
const { i18n } = _store.i18n;
interface FormData {
  number?: string;
  address?: string;
}

type Props = {
  userStore?: IUserStore;
  iStore?: any;
};

class InvestStore {
  @observable
  initialValues: FormData = {
    number: "",
    address: ""
  };

  @observable
  value: number = 0;

  // constructor(history:any){
  //   const params = new URLSearchParams(history.location.search);
  // }

  @action.bound
  async validate(values: FormData) {
    const jsonSchema = {
      number: string()
        .trim()
        // 数量格式不正确
        .matches(number, i18n("6"))
        // 数量不能为空
        .required(i18n("7")),
      address: string()
        .trim()
        // 格式不正确
        .matches(address, i18n("8"))
    };
    let errors = await validate(object().shape(jsonSchema), values);
    if (!values.address) delete errors.address;
    return errors;
  }

  @action.bound
  async handleSubmit(values: FormData) {
    values = jsonTrim(values);
    const { cobWeb, account, web3 } = await cobweb();
    const _wei = web3.utils.toWei(values.number);
    const address =
      values.address === "" ? store.userStore.ref : values.address;
    console.log(store.userStore);
    try {
      await cobWeb.methods
        .invest(account, _wei, address)
        .send({ from: account, value: _wei });
      // 入金成功
      toast.success(i18n("9"));
      store.userStore.init();
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async withdraw() {
    const { cobWeb, account } = await cobweb();
    try {
      await cobWeb.methods.withdraw().send({ from: account });
      // 提现成功
      toast.success(i18n("10"));
      store.userStore.init();
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  handleChange(event: React.ChangeEvent<{}>, newValue: number) {
    this.value = newValue;
  }
  @action.bound
  copyLink(link: string) {
    copy(link);
  }
}
