    backgroundImage: "url('./images/sum_1.png')",
    backgroundSize: "contain",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    color: "#2488E4",
    fontSize: 30,
    fontWeight: "bolder",
    margin: "0 auto 3vw auto",
    width: 300,
    height: 135,
    // height: (20*162/325) + "vw",
    display: "flex",
