import React from "react";

import IButton from "./common/IButton";
import { useLocalStore, useObserver } from "mobx-react";
import cobweb from "src/utils/cobweb";
import { error, isAddressNo } from "src/utils/tools";
import { toast } from "react-toastify";
import { action, observable } from "mobx";
import _store from "src/store";
const { i18n } = _store.i18n;

class FundWithdrawStore {
  @observable
  data = {
    idxInvest: "0",
    fundAddress: "0x0000000000000000000000000000000000000000",
    part: "",
    withdraw: "0"
  };

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobCore, account } = await cobweb();
      // console.log(cobWeb);
      const data = await cobCore.methods.getFund(account).call();

      if (!!data) {
        this.data = {
          fundAddress: data["fundAddress"],
          idxInvest: data["idxInvest"],
          part: data["part"],
          withdraw: data["withdraw"]
        };
      }
      // console.log(this.data);
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async withdraw() {
    const { cobWeb, account } = await cobweb();
    try {
      await cobWeb.methods.fundWithdraw("0").send({ from: account });
      toast.success(
        // "提现成功"
        i18n("1")
      );
    } catch (err) {
      error(err);
    }
  }
}

const store = new FundWithdrawStore();

const FundWithdraw: React.FC = () => {
  const iStore = useLocalStore(() => store);
  return useObserver(() => (
    <div>
      {!isAddressNo(iStore.data.fundAddress) && (
        <div>
          {/* <h3>角色用户</h3> */}
          {/* 角色用户提现 */}
      <IButton onTap={iStore.withdraw} color="#003DAA">{i18n("93")}</IButton>
        </div>
      )}
    </div>
  ));
};

export default FundWithdraw;
