import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useLocalStore, inject } from "mobx-react";
import { observer } from "mobx-react";
import { object, string } from "yup";
import { Formik } from "formik";
import { number, validate, address } from "src/utils/validate";
import Input from "./common/Input";
import IButton from "./common/IButton";
import { observable, action } from "mobx";
import cobweb from "src/utils/cobweb";
import { error, isAddressNo } from "src/utils/tools";
import SingleItem from "./common/SingleItem";
import IRules from "./common/IRules";
import _store from "src/store";
import clsx from "clsx";
import { Container } from "@material-ui/core";
import { toast } from "react-toastify";
import { IUserStore } from 'src/store/user';
const { i18n } = _store.i18n;

interface FormData {
  number?: string;
}

class InvestStore {
  @observable
  initialValues: FormData = {
    number: ""
  };

  @observable
  amount: string = "0";

  @observable
  choose: string = "single";

  @observable
  sin: string = "0";

  @observable
  dou: string = "0";

  @observable
  isbutton: boolean = false;

  @observable
  betting: boolean = false;

  @observable
  open: boolean = false;

  @observable
  userAddress: string = "";
  @observable
  partAmount: string = "";

  @observable
  msg: string = "";

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobCore, account, cobWeb, web3 } = await cobweb();
      const data = await cobCore.methods.getCurrentSindou().call();

      const _wdata = await cobWeb.methods
        .getSindouPartAmount(data["idxInvest"], 0)
        .call();
      this.partAmount = web3.utils.fromWei(_wdata["amount"]);
      // console.log(111);
      // console.log(data);
      await _store.userStore.init();
      this.sin = web3.utils.fromWei(data["sin"]);
      this.dou = web3.utils.fromWei(data["dou"]);
      const _data = await cobCore.methods
        .getUserSindou(_store.userStore.idSindou, account)
        .call();

      this.betting = data["id"] == _store.userStore.idSindou;

      const res = _data[0];
      this.userAddress = _data[1]["userAddress"];
      this.open = res != "0";
      const withdraw = _data[1]["withdraw"];

      if (this.open) {
        this.isbutton = res == _data[1]["bond"] && withdraw == "0";
      } else {
        this.isbutton = !isAddressNo(this.userAddress);
      }

      if (!isAddressNo(this.userAddress)) {
        if (res == _data[1]["bond"] && withdraw != "0" && this.open) {
          this.msg = `${i18n("103")}${web3.utils.fromWei(withdraw)}ETH`;
        } else if (res != _data[1]["bond"] && this.open) {
          this.isbutton = false;
          this.msg = i18n("104");
        } else {
          const sindou = await cobCore.methods
            .getSindou(_store.userStore.idSindou)
            .call();

          const _wdata = await cobWeb.methods
            .getSindouPartAmount(sindou["idxInvest"], 0)
            .call();
          console.log(2222)
          console.log(sindou)
          this.partAmount = web3.utils.fromWei(_wdata["amount"]);
          this.choose = _data[1]["bond"] == "1" ? "single" : "double";
          this.sin = web3.utils.fromWei(sindou["sin"]);
          this.dou = web3.utils.fromWei(sindou["dou"]);
          this.msg = `${i18n("102")}${web3.utils.fromWei(_data[1]["deposit"])}`;
          // this.sin = "1";
          // this.dou = "2";
          // this.partAmount = "4";
          // console.log(Number(this.sin), 1211111111)
        }
      }
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async validate(values: FormData) {
    const jsonSchema = {
      number: string()
        .trim()
        // 数量格式不正确
        .matches(number, i18n("42"))
        // 数量不能为空
        .required(i18n("43")),
      address: string()
        .trim()
        // 格式不正确
        .matches(address, i18n("44"))
    };

    let errors = await validate(object().shape(jsonSchema), values);
    // debugger
    return errors;
  }

  @action.bound
  async handleSubmit(values: FormData) {
    const { cobWeb, account, web3 } = await cobweb();
    try {
      const newSindou = this.choose == "single" ? 1 : 2;
      const _wei = web3.utils.toWei(values.number);
      await cobWeb.methods
        .newSindou(newSindou)
        .send({ from: account, value: _wei });
      // 提交成功
      toast.success(i18n("78"));
      _store.userStore.init();
      store.init();
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async withdraw() {
    const { cobWeb, account } = await cobweb();
    try {
      await cobWeb.methods
        .sindouWithdraw(_store.userStore.idSindou)
        .send({ from: account });
      toast.success(i18n("1"));
      _store.userStore.init();
      store.init();
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async changeStatus(values: string) {
    if (this.isbutton) return;
    this.choose = values;
  }
}
const useStyles = makeStyles(theme => ({
  bg: {
    backgroundImage: "url('./images/single_double.png')",
    backgroundSize: "cover",
    backgroundPosition: "center",
    paddingTop: "5vw",
    margin: "0 0 5vw 0"
  },
  logo: {
    width: 350,
    height: 106
  },
  box: {
    textAlign: "center",
    padding: "0 0 0 0 "
  },
  biglogo: {
    width: "83%"
  },
  rule: {
    fontSize: "1vw",
    color: "white",
    textAlign: "center",
    width: "80vw",
    margin: "0 auto 5vw auto"
  },
  single: {
    backgroundImage: "url('./images/single_double3.png')",
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    margin: "auto",
    width: "80vw",
    height: (80 * 2304) / 3210 + "vw",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: "0 12vw"
  },
  form: {
    width: "50%",
    margin: "auto"
  },
  button: {
    display: "flex",
    justifyContent: "center"
  },
  bate: {
    width: "3vw",
    margin:"0 auto",
  },
  titleBox:{
    display:"flex",
  },
  evenItem: {
    background: "#36AADB",
    width: "22vw",
    cursor: "pointer"
  },
  active: {
    background: "#3747BC"
  }
}));
const store = new InvestStore();
type Props = {
  userStore?: IUserStore;
  iStore?: any;
};

const SingleDouble: React.FC<Props> = inject((allStores: any) => ({
  userStore: allStores.userStore as IUserStore,
  iStore: new InvestStore()
}))(
  observer(({ userStore, iStore }: Props) => {
    const classes = useStyles();
    return (
      <div className={classes.bg}>
        <div className={classes.box}>
          {/* "./images/single_double2.png" */}
          <img src={i18n("90")} alt="" className={classes.logo} />
        </div>
        {/* 平台是实时开奖，单双奖池的95%将奖励给投资赢的玩家 */}
        {/* <IRules content={i18n("45")} /> */}
        <div className={classes.single}>
          <>
            {/* 单 */}
            <div
              className={clsx(classes.evenItem, {
                [classes.active]: iStore.choose == "single"
              })}
              onClick={() => {
                iStore.changeStatus("single");
              }}
            >
              <SingleItem number={iStore.sin} content="/images/banma.png" />
            </div>
            {/* 双 */}
            <div
              className={clsx(classes.evenItem, {
                [classes.active]: iStore.choose == "double"
              })}
              onClick={() => {
                iStore.changeStatus("double");
              }}
            >
              <SingleItem number={iStore.dou} content="/images/shizi.png" />
            </div>
          </>
        </div>
      
          {
            Number(iStore.sin) + Number(iStore.dou) + Number(iStore.partAmount) >= 6 && (
              <Container className={classes.titleBox}>
                <img src="./images/bate.png" alt="" className={classes.bate} />
              </Container>
            )
          }
          <IRules content={`${i18n("105")}:${iStore.partAmount}`} />
     

        <IRules content={iStore.msg} />


        {!iStore.isbutton && (
          <div className={classes.form}>
            <Formik
              initialValues={iStore.initialValues}
              validate={iStore.validate}
              onSubmit={iStore.handleSubmit}
              {...iStore}
              render={({ submitForm }) => (
                <form>
                  {/* 数量 */}
                  <Input name="number" placeholder={i18n("48")} />
                  <div className={classes.button}>
                    {/* 发送ETH */}
                    <IButton onTap={submitForm} color="#003DAB">
                      {i18n("49")}{" "}
                    </IButton>
                  </div>
                </form>
              )}
            />
          </div>
        )}
        {iStore.isbutton && (
          <IButton onTap={iStore.withdraw} color="#003DAB">
            {i18n("30")}
          </IButton>
        )}
      </div>
    );
  })
);

export default SingleDouble;
