import React from "react";
import { Field } from "formik";
import { TextField } from "formik-material-ui";
import {
  InputAdornment,
  IconButton,
  Button,
  FormControl,
  InputLabel,
  FilledInput,
  Theme,
  Container
} from "@material-ui/core";
import { values } from "mobx";
import { makeStyles } from "@material-ui/styles";

type Props = {
  placeholder?: string;
  name: string;
  noEndAdornment?: boolean;
};

const useStyles = makeStyles((theme: Theme) => ({
  input: {
    width: "100%",
    marginBottom: 10
  },
  end: {
    background: "#3747BC",
    color: "white",
    padding: "0.7vw",
    fontSize: "1vw",
    borderRadius: "0.5vw"
  }
}));
const Input: React.FC<Props> = ({ placeholder, name, noEndAdornment }) => {
  const classes = useStyles();
  return (
    <Field
      name={name}
      label={placeholder}
      type="text"
      // component={}
      render={(props: any) => {
        return (
          <TextField
            className={classes.input}
            variant="outlined"
            color="primary"
            InputProps={{
              endAdornment: (
                <>
                  {!noEndAdornment && (
                    <InputAdornment position="end">
                      <Container className={classes.end}>
                        {name == "number" ? "ETH" : "address"}
                      </Container>
                    </InputAdornment>
                  )}
                </>
              )
            }}
            {...props}
          />
        );
      }}
    />
  );
};

export default Input;
