import React from "react";

import {
  Typography,
  Box,
  Theme,
  Tabs,
  Tab,
  createStyles
} from "@material-ui/core";
import { withStyles } from "@material-ui/styles";

export interface ITabPanelProps {
  children?: React.ReactNode;
  index: any;
  value: any;
  className: any;
}

export interface ITabStyledProps {
  label: string;
}




export const ITabs = withStyles({
  root: {
    background: "#FFC46B",
    fontSize: 16,
    fontWeight: "bolder"
  }
})(Tabs);

export const ITab = withStyles((theme: Theme) =>
  createStyles({
    root: {
      backgroundColor: "#FFC46B",
      fontWeight: "bolder",
      fontSize: 16,
      "&:hover": {
        color: "white",
        opacity: 1
      },
      "&$selected": {
        color: "white",
        fontWeight: "bolder",
        fontSize: 16,
        background: "linear-gradient(45deg, #333333 30%, #262422 90%)"
      },
      "&:focus": {
        // color: "#40a9ff"
      }
    },
    selected: {}
  })
)((props: ITabStyledProps) => <Tab disableRipple {...props} />);


export const ITabPanel = (props: ITabPanelProps) => {
  const { children, value, index, className, ...other } = props;

  return (
    <Typography
      component="div"
      hidden={value !== index}
      className={className}
      {...other}
    >
      <Box p={3}>{children}</Box>
    </Typography>
  );
};
