import React from "react";
import { useLocalStore, useObserver, observer, inject } from "mobx-react";
import { object, string } from "yup";
import { Formik } from "formik";
import { number, validate, jsonTrim, address } from "src/utils/validate";
import Input from "./common/Input";
import IButton from "./common/IButton";
import { observable, action } from "mobx";
import cobweb from "src/utils/cobweb";
import { error, dateFormat, isAddressNo } from "src/utils/tools";
import { Theme, Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/styles";
import { ITabs, ITab, ITabPanel } from "./common/ITab";
import IRules from "./common/IRules";
import ILine from "./common/ILine";
import { toast } from "react-toastify";
import _store from "src/store";
import { IUserStore, UserStore } from "src/store/user";

const { i18n } = _store.i18n;
interface FormData {
  number?: string;
  address?: string;
}

class InvestStore {
  @observable
  initialValues: FormData = {
    number: ""
  };

  @observable
  deposit: string = "0";
  @observable
  goal: string = "0";
  @observable
  status: string = "0";
  @observable
  dueTime: string = "0";
  @observable
  crowd: string = "0";
  @observable
  userAddress:string = "";

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobWeb, cobCore, account, web3 } = await cobweb();
      const crowd = await cobCore.methods.getCrowd(account).call();
      this.deposit = web3.utils.fromWei(crowd["deposit"]);
      this.goal = web3.utils.fromWei(crowd["goal"]);
      this.status = crowd["status"];
      this.dueTime = crowd["dueTime"];
      this.userAddress = crowd["userAddress"];
      // const partAmount = await cobWeb.methods
      //   .getPartAmount(crowd["idxInvest"], 0)
      //   .call();
      // this.crowd = web3.utils.fromWei(partAmount["crowd"]);
      console.log(crowd);
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async validate(values: FormData) {
    const jsonSchema = {
      number: string()
        .trim()
        // 数量格式不正确
        .matches(number, i18n("32"))
        // 数量不能为空
        .required(i18n("33")),
      address: string()
        .trim()
        // 格式不正确
        .matches(address, i18n("34"))
    };
    let errors = await validate(object().shape(jsonSchema), values);
    // debugger
    return errors;
  }

  @action.bound
  async handleSubmit(values: FormData) {
    // console.log(values);

    const { cobWeb, account, web3 } = await cobweb();
    const _wei = web3.utils.toWei(values.number);

    try {
      await cobWeb.methods.initCrowdSale().send({ from: account, value: _wei });
      // 提交成功
      toast.success(i18n("78"));
      store.init();
    } catch (err) {
      error(err);
    }
  }

  @action.bound
  async withdraw() {
    const time = new Date().getTime() / 1000;
    // if (parseInt(this.dueTime) > time) {
    //   console.log(i18n("82"));
    //   // 截止时间以后才能提现
    //   toast.success(i18n("82"));
    // } else {
    const { cobWeb, account } = await cobweb();
    await cobWeb.methods.crowdWithdraw().send({ from: account });
    toast.success(
      // "提现成功"
      i18n("1")
    );
    // }
  }
}

const store = new InvestStore();

const useStyles = makeStyles((theme: Theme) => ({
  container: {
    backgroundImage: "url('./images/raisebg.png')",
    backgroundSize: "cover",
    backgroundPosition: "center"
  },
  root: {
    backgroundColor: "#3747BC",
    padding: 0
    // width: "70vw",
    // margin: "5vw auto"
  },
  tabContainer: {
    margin: "5vw auto"
  },
  logo: {
    width: 350,
    height: 106
  },
  box: {
    textAlign: "center",
    padding: "5vw 0 2vw 0"
  },
  tab: {
    backgroundColor: "#FFC46B",
    color: "black"
  },
  TabPanel: {
    background: "#3747BC"
  },
  rule: {
    fontSize: "1vw",
    color: "white",
    textAlign: "center",
    width: "80vw",
    margin: "0 auto 5vw auto"
  }
}));

type Props = {
  userStore?: IUserStore;
  iStore?: any;
};
const Raise: React.FC<Props> = inject((allStores: any) => ({
  userStore: allStores.userStore as IUserStore,
  iStore: new InvestStore()
}))(
  observer(({ userStore, iStore }: Props) => {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
      setValue(newValue);
    };

    return (
      <div className={classes.container}>
        <div className={classes.box}>
          {/* ./images/raise.png */}
          <img src={i18n("89")} alt="" className={classes.logo} />
        </div>
        {/* 众筹发起到结束时长为是2个月，时长到期未众筹成功则算失败,失败后投入的众筹资金会在2个月后退回 */}
        <IRules content={i18n("35")} />
        <Container className={classes.tabContainer}>
          <div className={classes.root}>
            <ITabs
              value={value}
              onChange={handleChange}
              aria-label="ant example"
              centered={true}
              variant="fullWidth"
            >
              {/* 众筹 */}
              <ITab label={i18n("36")} />
              {/* 加入 */}
              {/* <ITab label={ i18n("37")} /> */}
              {/* 发起 */}
              <ITab label={i18n("19")} />
            </ITabs>
            <ITabPanel value={value} index={0} className={classes.TabPanel}>
              {isAddressNo(iStore.userAddress) && (
                <Formik
                  initialValues={iStore.initialValues}
                  validate={iStore.validate}
                  onSubmit={iStore.handleSubmit}
                  {...iStore}
                  render={({ submitForm }) => (
                    <form>
                      {/* 数量 */}
                      <Input name="number" placeholder={i18n("39")} />
                      {/* 发送众筹 */}
                      <IButton onTap={submitForm}>{i18n("40")}</IButton>
                    </form>
                  )}
                />
              )}

              {!isAddressNo(iStore.userAddress) && (
                <>
                  {/* 押金 */}
                  <ILine title={i18n("79")} content={iStore.deposit} />
                  {/* 众筹金额 */}
                  <ILine title={i18n("80")} content={iStore.goal} />
                  {/* 截止时间 */}
                  <ILine
                    title={i18n("81")}
                    content={dateFormat("YYYY-mm-dd HH:MM:SS", iStore.dueTime)}
                  />
                   {/* 状态 */}
                   {iStore.status =="2" &&  <ILine
                    title={`${i18n("106")}:`}
                    content={i18n("107")}
                  /> }
                 
                  {/* 状态 */}
                  {iStore.status =="3" &&  <ILine
                    title={`${i18n("106")}:`}
                    content={i18n("108")}
                  /> }
                 

                  {iStore.status =="1" && <IButton onTap={iStore.withdraw}>提现</IButton>} 
                </>
              )}
            </ITabPanel>
            <ITabPanel value={value} index={1} className={classes.TabPanel}>
              {/* 1.众筹金额需100ETH起筹 */}
              <ILine title={i18n("72")} />
              {/* 2.发起众筹需先缴纳10%作为押金，众筹成功的时退还押金，众筹失败时2个月后退还押金 */}
              <ILine title={i18n("73")} />
              {/* 3.众筹发起到结束时长为2个月,当时长到期到未众筹成功则本次众筹失败,众筹失败时投入的众筹资金2个月后(从众筹失败后开始算)将返回给众筹者 */}
              <ILine title={i18n("74")} />
              {/* 4.会在2个月后原路返回资金 */}
              <ILine title={i18n("75")} />
            </ITabPanel>
            {/* <ITabPanel value={value} index={2} className={classes.TabPanel}>
        { i18n("41")}
        </ITabPanel> */}
          </div>
        </Container>
        <IRules content={`${i18n("105")}:${userStore!.crowd}`} />
      </div>
    );
  })
);

export default Raise;
