import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import ISumLine from "./common/ISumLine";



const useStyles = makeStyles(theme => ({
    root: {
        backgroundImage: "url('./images/sum_2.png')",
        backgroundSize: "contain",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: "30vw",
        color: "white",
        minWidth: 320,
        minHeight: (320 * 825 / 652),
        height: (30 * 825 / 652) + "vw",
    },
    title: {
        color: "white",
        fontSize: "1.5vw",
        fontWeight: "bolder",
        borderBottomWidth: "0.1vw",
        borderBottomStyle: "solid",
        borderBottomColor: "white",
        textAlign: "center",
        margin: 0
    },
    content: {
        fontSize: 16,
        color: "white",
        wordBreak: "break-all"
    },
    box: {
        width: "18vw",
        height: (18 * 825 / 652) + "vw",
        minWidth: 200,
        minHeight: (200 * 825 / 652),
        margin: 0,
    }
}));

interface Props {
    title: string;
    content?: any;
}
const SumItem: React.FC<Props> = ({ title, content }: Props) => {
    const classes = useStyles();
    return (
        <div className={classes.root}>
            <div className={classes.box}>
                <p className={classes.title}>{title}</p>

                {
                    content.map((item: any,i:number) => {
                        return <ISumLine
                            title={item.title}
                            content={item.content}
                            key={i}
                        />
                    })
                }
            </div>
        </div>
    );
};
export default SumItem;
